import * as Yup from 'yup';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { useSnackbar } from 'notistack5';
import { useNavigate } from 'react-router-dom';
import { Form, FormikProvider, useFormik } from 'formik';

// material
import { LoadingButton } from '@material-ui/lab';
import { Box, Card, Grid, Stack, Button } from '@material-ui/core';
// paths
import { PATH_DASHBOARD } from '../../../routes/paths';
// utils
import { nameRegExp } from '../../../utils/constants/regexs';
// components
import Steps from '../../Steps';
import CampaignTypeForm from '../CampaignTypeForm';
import TaskAndRewardsForm from './TaskAndRewardsForm';
import TaskDetailForm from './TaskDetailForm';

// ----------------------------------------------------------------------

const STEPS = ['Campaign Type', 'Task & Rewards', 'Task Detail'];

function TaskNewForm({ isEdit, currentTask }) {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();
  const [activeStep, setActiveStep] = useState(0);

  const CampaignTypeSchema = Yup.object().shape({
    brand: Yup.string().matches(nameRegExp, 'Cannot include special characters.').required('Brand is required'),
    campaignType: Yup.string(),
  });

  const TaskAndRewardsSchema = Yup.object().shape({
    name: Yup.string().matches(nameRegExp, 'Cannot include special characters.').required('Name is required'),
    taskType: Yup.string(),
    totalBudget: Yup.number(),
    perUser: Yup.string(),
    starPerUser: Yup.number(),
    continueAfterBudget: Yup.string(),
    tier: Yup.string(),
    isPublic: Yup.boolean().required('Status is required'),
  });

  const TaskDetailSchema = Yup.object().shape({
    headerImage: Yup.mixed(),
    description: Yup.string(),
    doList: Yup.array().of(Yup.string()),
    dontList: Yup.array().of(Yup.string()),
    igCaptionQuestions: Yup.array().of(Yup.string()),
    noOfPhotoRequired: Yup.number(),
    noOfVideoRequired: Yup.number(),
    hashtags: Yup.array().of(Yup.string()),
    mentions: Yup.array().of(Yup.string()),
    startDate: Yup.date().nullable(true),
    endDate: Yup.date().when('startDate', (startDate, schema) =>
      startDate ? schema.min(startDate, 'End date must be later than start date') : schema.nullable(true)
    ),
  });

  const VALIDATION_SCHEMAS = [CampaignTypeSchema, TaskAndRewardsSchema, TaskDetailSchema];

  const isLastStep = () => activeStep === STEPS.length - 1;

  const handlePrevStep = () => setActiveStep((prev) => prev - 1);

  const handleNextStep = () => setActiveStep((prev) => prev + 1);

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      brand: currentTask?.brand || '',
      campaignType: currentTask?.type || '',
      name: currentTask?.name || '',
      taskType: currentTask?.taskType || '',
      totalBudget: currentTask?.totalBudget || undefined,
      perUser: currentTask?.perUser || '',
      starPerUser: currentTask?.starPerUser || undefined,
      continueAfterBudget: currentTask?.continueAfterBudget || '',
      tier: currentTask?.tier || '',
      isPublic: currentTask?.isPublic || false,
      headerImage: currentTask?.headerImage || undefined,
      doList: currentTask?.doList || [''],
      dontList: currentTask?.dontList || [''],
      igCaptionQuestions: currentTask?.igCaptionQuestions || [''],
      noOfPhotoRequired: currentTask?.noOfPhotoRequired || undefined,
      noOfVideoRequired: currentTask?.noOfVideoRequired || undefined,
      hashtags: currentTask?.hashtags || [],
      mentions: currentTask?.mentions || [],
      startDate: currentTask?.startDate ? new Date(currentTask.startDate) : null,
      endDate: currentTask?.endDate ? new Date(currentTask.endDate) : null,
    },
    validationSchema: VALIDATION_SCHEMAS[activeStep],
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      if (isLastStep()) {
        try {
          alert(JSON.stringify(values));
          resetForm();
          setSubmitting(false);
          enqueueSnackbar(!isEdit ? 'Create success' : 'Update success', { variant: 'success' });
          navigate(PATH_DASHBOARD.task.list);
        } catch (error) {
          console.error(error);
          setSubmitting(false);
          setErrors(error);
        }
      } else {
        handleNextStep();
      }
    },
  });

  const { errors, values, handleSubmit, isSubmitting } = formik;

  console.log('Active Step: ', activeStep);
  console.log('*** ', values, errors);

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container justifyContent="center">
          <Grid item xs={12} md={8} sx={{ mb: 5 }}>
            <Steps steps={STEPS} activeStep={activeStep} />
          </Grid>
        </Grid>

        <Card sx={{ p: 3 }}>
          <Stack spacing={3}>
            {activeStep === 0 && <CampaignTypeForm formik={formik} />}
            {activeStep === 1 && <TaskAndRewardsForm formik={formik} />}
            {activeStep === 2 && <TaskDetailForm formik={formik} />}
            <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
              {activeStep !== 0 && (
                <Button
                  sx={{ mr: 2 }}
                  variant="outlined"
                  onClick={isLastStep() ? () => console.log('Draft Clicked') : handlePrevStep}
                >
                  {isLastStep() ? 'Draft' : 'Back'}
                </Button>
              )}
              <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                {isLastStep() ? 'Setup Complete' : 'Next'}
              </LoadingButton>
            </Box>
          </Stack>
        </Card>
      </Form>
    </FormikProvider>
  );
}

TaskNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentTask: PropTypes.object,
};

export default TaskNewForm;
