import { useParams, useLocation } from 'react-router-dom';
// material
import { Container } from '@material-ui/core';
// routes
import { PATH_DASHBOARD } from '../routes/paths';
// hooks
import useSettings from '../hooks/useSettings';
// components
import Page from '../components/Page';
import HeaderBreadcrumbs from '../components/HeaderBreadcrumbs';
import TaskNewForm from '../components/campaign/task/TaskNewForm';

// ----------------------------------------------------------------------

function TaskCreate() {
  const { themeStretch } = useSettings();
  const { pathname } = useLocation();
  const { name } = useParams();
  const isEdit = pathname.includes('edit');

  const currentTask = {};

  return (
    <Page title="Task: Create a new task">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={!isEdit ? 'Create Task' : 'Edit Task'}
          links={[{ name: 'Task', href: PATH_DASHBOARD.task.list }, { name: !isEdit ? 'Create Task' : name }]}
        />
        <TaskNewForm isEdit={isEdit} currentTask={currentTask} />
      </Container>
    </Page>
  );
}

export default TaskCreate;
